 :root {
   --small-green-circle: #78eea6;
   --main-accent-color: #9b45e4;
   --secondary-accent-color: #e13a9d;
 }
 
 button {
   font-family: inherit;
   font-size: 100%;
   line-height: 1.15;
   margin: 0;
   overflow: visible;
   -webkit-appearance: button;
 }
 
 button::-moz-focus-inner,
 [type="button"]::-moz-focus-inner {
   border-style: none;
   padding: 0;
 }
 

 
 .container {
   display: grid;
   justify-content: center;
   align-items: center;
   height: 100vh;
   text-align: center;
   background-color:white;
   width: 100%; 
 }
 
 .card {
   padding: 1em;
   border-radius: 0.8em;
   background-color:white;
   box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.02),
     0 6.7px 5.3px rgba(0, 0, 0, 0.028), 0 12.5px 10px rgba(0, 0, 0, 0.035),
     0 22.3px 17.9px rgba(0, 0, 0, 0.042), 0 41.8px 33.4px rgba(0, 0, 0, 0.05),
     0 100px 80px rgba(0, 0, 0, 0.07);
   position: relative;
   
 }

 .card__image {
   margin: auto;
   margin-top: 40px;
   margin-bottom: 40px;
   width: 150px;
   height: 150px;
   border-radius: 50%;
 }
 
 .card__info {
   margin: 1em 0;
   list-style-type: none;
   padding: 0;
 }
 
 .card__info li {
   display: inline-block;
   text-align: center;
   padding: 0.5em;
 }
 
 .card__info__stats {
   color: #51c57f;
   font-weight: bold;
   font-size: 1.2em;
   display: block;
 }
 
 .card__info__stats + span {
   color: #969798;
   text-transform: uppercase;
   font-size: 0.8em;
   font-weight: bold;
 }
 
 .card__text h2 {
   margin-bottom: 0.3em;
   margin-top: 0.45em;
   font-size: 1.4em;
   color: #6f6f6f;
 }
 
 .card__text p {
   margin: 0;
   color: #999;
   font-size: 0.80em;
 }
 
 .card__action {
   display: flex;
   justify-content: space-around;
 }
 
 .card__action__button {
   padding: 0.9em 1.3em;
   text-transform: uppercase;
   color: #fff;
   border: none;
   border-radius: 0.5em;
   cursor: pointer;
   position: relative;
 }
 
 .card__action__button:before {
   content: "";
   position: absolute;
   border-radius: 0.5em;
   transition: all 0.35s ease-in-out;
 }
 
 .card__action__button:hover:before {
   top: -6px;
   bottom: -6px;
   left: -6px;
   right: -6px;
 }
 
 .card__action--follow {
   background-color: #9b45e4;
   background-color: var(--main-accent-color);
 }
 
 .card__action__button:hover:before {
   border: 4px solid #9b45e4;
   border: 4px solid var(--main-accent-color);
 }
 
 .card__action--message {
   background-color: #e13a9d;
   background-color: var(--secondary-accent-color);
 }
 
 .card__action--message:hover:before {
   border: 4px solid #e13a9d;
   border: 4px solid var(--secondary-accent-color);
 }
 
